import Home from '../components/home.vue'
import album_1 from '../components/albums/album_1.vue'
import album_2 from '../components/albums/album_2.vue'
import album_3 from '../components/albums/album_3.vue'
import album_4 from '../components/albums/album_4.vue'
import album_5 from '../components/albums/album_5.vue'
import album_6 from '../components/albums/album_6.vue'
import contact from '../components/contact.vue'

export default [{
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/album_1',
    name: 'album_1',
    component: album_1
  },
  {
    path: '/album_2',
    name: 'album_2',
    component: album_2
  },
  {
    path: '/album_3',
    name: 'album_3',
    component: album_3
  },
  {
    path: '/album_4',
    name: 'album_4',
    component: album_4
  },
  {
    path: '/album_5',
    name: 'album_5',
    component: album_5
  },
  {
    path: '/album_6',
    name: 'album_6',
    component: album_6
  },
  {
    path: '/contact',
    name: 'contact',
    component: contact
  }
]



