<template>
<v-app id="app" v-cloak>
  <div>
    <transition name="fade">
      <router-view/>
    </transition>
  </div>
  <homeView></homeView>
</v-app>
</template>
<script>
import homeView from './components/homeComponent'


export default {
  name: 'app',
  components: {
    homeView,
  }

}
</script>
<style media="screen" >
.application.theme--light {
  background: #fcfcfc !important;
}

img {
  max-width: 100%;
  height: auto;
}

.mainContainer {
  max-width: 1000px !important;
  padding: 0 !important;
}

.homeContainer {
  max-width: 880px !important;
  padding: 0;
}

.mainToolbar {
  height: 80px;
  background-color: #fcfcfc !important;
}

.rowz {
  line-height: 80px !important;
}

.titleMain,
.contact {
  cursor: pointer;
  text-decoration: none;
  color: black;
}

.margtop {
  margin-top: 110px !important;
}

.fade-enter-active {
  transition: .3s ease-in-out;
}

.fade-leave-active,
.fade-enter {
  transform: translatex(-3%);
  filter: blur(20px);
}

[v-cloak] {
  display: none
}
</style>



