<template lang="html">
  <v-app class="page">
    <v-container class="mainContainer margtop" ref="guda">
      <v-layout row class="pic mt-3" v-for="album in albums">
        <v-flex xs12>
          <clazy-load :src="album.imgURL">
          <!-- The image slot renders after the image loads. -->
            <img :src="album.imgURL" slot="image">
          <!-- The placeholder slot displays while the image is loading. -->

            <v-flex class="margtop  text-xs-center" slot="placeholder" id="load">
              <v-progress-circular indeterminate color="primary" :width="7" :size="100"  ></v-progress-circular>
            </v-flex>

          </clazy-load>
          <h5 class="text-xs-center">{{album.text}}</h5>
        </v-flex>
      </v-layout>

    </v-container>
  </v-app>
</template>

<script>
export default {
  data() {
    return {
      albums: [{
          imgURL: '../static/album_1/pic_1.jpg',
          text: 'Picture Title',
        },
        {
          imgURL: '../static/album_1/pic_2.jpg',
          text: 'Picture Title',
        },
        {
          imgURL: '../static/album_1/pic_3.jpg',
          text: 'Picture Title',
        },
        {
          imgURL: '../static/album_1/pic_4.jpg',
          text: 'Picture Title',
        },
        {
          imgURL: '../static/album_1/pic_5.jpg',
          text: 'Picture Title',
        },
        {
          imgURL: '../static/album_1/pic_6.jpg',
          text: 'Picture Title',
        }
      ]
    }
  }

}
</script>

<style scoped lang="css">

</style>



