<template lang="html">
  <v-app class="page">
    <v-container class="mainContainer margtop">
      <v-layout row class="text-xs-center pic mt-3" v-for="album in albums">
        <v-flex xs12>
          <clazy-load :src="album.imgURL">
          <!-- The image slot renders after the image loads. -->
            <img :src="album.imgURL" slot="image">
          <!-- The placeholder slot displays while the image is loading. -->
            <v-flex class="mt-5 text-xs-center" slot="placeholder" id="load">
              <v-progress-circular indeterminate color="primary" :width="7" :size="100"  ></v-progress-circular>
            </v-flex>
          </clazy-load>
          <h5 class="text-xs-center">{{album.text}}</h5>
        </v-flex>
      </v-layout>
    </v-container>
  </v-app>
</template>

<script>
export default {
  data() {
    return {
      albums: [{
          imgURL: '../static/placeholder1000.png',
          text: 'Picture Title',
        },
        {
          imgURL: '../static/Placeholder800x1000.png',
          text: 'Picture Title',
        },
        {
          imgURL: '../static/placeholder-1000x500.png',
          text: 'Picture Title',
        },
        {
          imgURL: '../static/placeholder1000.png',
          text: 'Picture Title'
        }]
    }
  }
}
</script>

<style scoped lang="css">
  .pic{

  }
</style>



